.confirmation-dialog-wrapper {
  height: 736px;
  border-radius: 28px;
  // border: 1px solid red;
  // width: 300px;
  // height: calc(100vh - 48px);
  /* padding: 24px;
  height: 661px; */
  // overflow-y: hidden;

  // .mat-mdc-dialog-content {
  //   max-height: 76vh;
  //   overflow-y: scroll;
  // }

  .header-container {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 24px;
    padding-left: 24px;
    padding-right: 24px;
    padding-top: 24px;

    // justify-content: flex-end;

    .mdc-dialog__title::before {
      height: 0;
    }

    .dialog-title {
      margin: 0;
      padding: 0;
      margin-left: -7px;
      // font-weight: 400;
    }
  }

  .label {
    margin-bottom: 8px;
    // color: var(--mdc-outlined-text-field-label-text-color);
  }

  .program-name {
    // font-weight: 400;
    // color: black;
  }

  .program-header {
    // margin-bottom: 24px;
  }

  .dialog-content {
    // background-color: aqua;
    // overflow: hidden;
    // border-radius: 16px;
    // height: calc(100vh - 300px);
    // overflow-y: scroll;

    // margin-top: 16px;
    // padding-bottom: 24px;
    // padding: 0;
    padding-left: 24px;
    padding-right: 24px;
    padding-bottom: 16px;
    padding-top: 0;
    flex-grow: 1;
    // max-height: 85vh;
    height: 552px;

    .no-handle {
      cursor: pointer;
    }

    .no-margin {
      margin-left: 0;
      margin-right: 0;
      flex-basis: 274px;
    }

    .program-panel {
      // background: #f7f9ff;
      border-radius: 0;
      border: none !important;

      &:first-of-type {
        margin-top: 8px;
        border-top-left-radius: 16px;
        border-top-right-radius: 16px;
      }

      &:last-of-type {
        border-bottom: none !important;
        border-bottom-left-radius: 16px;
        border-bottom-right-radius: 16px;
      }
    }

    .program-img-wrapper {
      // height: 30%;
      padding: 8px 16px 8px 0;
      width: 80px;
      flex-grow: 0;

      .selected-exercise-img {
        width: 100%;
        // height: 100%;

        // object-fit: cover;
      }
    }

    .program-panel-description {
      justify-content: flex-end;
    }

    .program-panel-content {
      flex-direction: column;

      .program-description {
        gap: 0;
      }

      .program-exercise-session {
        flex-direction: row;
      }
    }
  }

  .program-dialog-actions {
    // padding: 24px;
    // padding-top: 24px;
    margin-top: auto;
    justify-content: end;
  }
}
